.exchanges-table{
    width: 100%;
    border-collapse: collapse;
}
.exchanges-table a {
    font-size: 1.1vw;
    text-decoration: 0;
    text-transform: uppercase;
}
.exchanges-table th{
    background: rgba(154, 168, 172, 0.9);
    text-transform: uppercase;
    font-size: 1.2vw;
}
.flex-th-price,.flex-th{
    display: flex;
    justify-content: space-evenly;
}
.flex-th-price td{
    font-size: 1.2vw;
    background: rgba(226, 225, 225, 0.5);
    background: rgba(226, 225, 225, 0.5);
    border-left: 1px solid #333;
    width: 100%;
}
.flex-th-price td:nth-child(1){
    border-left: 0;
    background: 0;
    background-image:linear-gradient(to right, transparent, rgba(226, 225, 225, 0.5))
}
.flex-th-price td:nth-child(3){
    background: 0;
    background-image:linear-gradient(to left, transparent, rgba(226, 225, 225, 0.5))
}
.flex-th-price td:hover{
    background-color: rgb(221, 221, 221);
}

.exchanges-table tr{
    border-bottom: 1px solid black;
}
.exchanges-table tr:hover{
    background-color: rgba(230, 230, 230, 0.418);
}
.exchanges-table td {
    padding: .25vw .5vw .25vw 1vw;
    font-size: 1.1vw;
}
.exchanges-table td:nth-child(6),
.exchanges-table td:nth-child(7),
.exchanges-table td:nth-child(8) {
    background: #eee;
}
.exchanges-table td:nth-child(9),
.exchanges-table td:nth-child(10),
.exchanges-table td:nth-child(11) {
    background: rgb(228, 228, 228);
}
.flex-td{
    display: flex;
    justify-content: left;
    width: 20vw;
}
.exchange-img{
    margin: 0 .25vw;
}
