}
.serach-box{
    -webkit-flex: 1 1;
            flex: 1 1;
}
.serach-box input {
    padding: .05vw 0vw;
    outline: 0;
    margin: 0 auto;
    float: right;
    border: 1px solid rgb(236, 236, 236);
