}
.card-name{
  -webkit-flex-direction: column;
          flex-direction: column;
}
.table .rank{
  padding: .25vw .5vw;
  border-radius: .25vw;
  font-size: calc(.5rem + .5vw);
  background-color: #333;
  position: relative;
  top: 0;
  color: #eee;
}
.homepage{
    position: relative;
}
